/**
 * Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð²Ñ‹Ð±Ð¾Ñ€Ð° ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²
 * @param {HTMLElement} element ÑÐ¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ð¹ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ ÐºÐ»Ð°ÑÑÐ° DropDown
 * @param {string} content placeholer Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ð¹ Ð¸Ð· Ð½Ð°ÑÑ‚Ñ€Ð¾ÐµÐº ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {object} styles Ð½Ðµ Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ð¹ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€. ÐžÐ±ÑŠÐµÐºÑ‚ Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ Ð½Ð°Ñ…Ð¾Ð´ÑÑ‚ÑŒÑÑ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ð¸ Ñ‡Ð°ÑÑ‚ÐµÐ¹ ÑÐµÐ»ÐµÐºÑ‚Ð°
 */
export function createSelected(element, content, styles) {
  if (content) {
    element.innerHTML = `
      <div class="cg-select">
         <p class="selected">${content}</p>
          <div class="caret"></div>
       </div>
      `;
  }

  if (styles) {
    customStyles(element, styles);

    element.innerHTML = `
      <div class="cg-select" style = "${styles}">
          <p class="selected" style = "${styles}">${content}</p>
          <div class="caret" style = "${styles}"></div>
      </div>
    `;
  }
}

/**
 * ÐŸÐ¾Ð¸ÑÐº Ð¸ ÑÑ‚Ð¸Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð½Ñ‹Ñ… Ð¸Ð· styles ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° DropDown
 * @param {HTMLElement} element ÑÐ¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ð¹ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ ÐºÐ»Ð°ÑÑÐ° DropDown
 * @param {object} styles Ð¾Ð±ÑŠÐµÐºÑ‚ Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ Ð½Ð°Ñ…Ð¾Ð´ÑÑ‚ÑŒÑÑ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ð¸ Ñ‡Ð°ÑÑ‚ÐµÐ¹ ÑÐµÐ»ÐµÐºÑ‚Ð°
 */
export function customStyles(element, styles) {
  if (!styles) {
    return;
  }

  const { head, caret, placeholder, lable } = styles;

  const cgSelect = element.querySelector('.cg-select');
  const caretSelect = element.querySelector('.caret');
  const placeholderSelect = element.querySelector('.selected');
  const lableItem = element.parentElement.querySelector('h1.label');

  customStylesFormat(head, cgSelect);
  customStylesFormat(caret, caretSelect);
  customStylesFormat(lable, lableItem);

  if (placeholderSelect) {
    customStylesFormat(placeholder, placeholderSelect);
  }
}

/**
 * Ð£Ð½Ð¸Ð²ÐµÑ€ÑÐ°Ð»ÑŒÐ½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ Ð´Ð»Ñ ÑÑ‚Ð¸Ð»Ð¸Ð·Ð°Ñ†Ð¸Ð¸ ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {object} elemOption Ð¾Ð±ÑŠÐµÐºÑ‚ Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð½Ð¾Ðµ Ð¸Ð· Ð¾Ð±ÑŠÐµÐºÑ‚Ð° styles Ñƒ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð³Ð¾ Ð¼Ñ‹ Ð¿Ð¾Ð»ÑƒÑ‡Ð°ÐµÐ¼ ÐºÐ»ÑŽÑ‡-Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ ÑÑ‚Ð¸Ð»ÐµÐ¹
 * @param {HTMLElement} selector  HTMLElement Ð¿Ð¾Ð´Ð²ÐµÑ€Ð³Ð°ÑŽÑ‰Ð¸ÐµÑÑ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ð¸
 */
export function customStylesFormat(elemOption, selector) {
  if (elemOption) {
    Object.entries(elemOption).forEach(([key, value]) => {
      selector.style[key] = value;
    });
  }
}

/**
 * ÐŸÑ€Ð¾Ð²ÐµÑ€ÐºÐ° ÑÐ¾Ð´ÐµÑ€Ð¶Ð¸Ñ‚ Ð»Ð¸ item  ÑƒÐºÐ°Ð·Ð°Ð½Ð½Ñ‹Ðµ ÑÐ²Ð¾Ð¹ÑÑ‚Ð²Ð°,
 * @param {object} item Ð¿Ñ€Ð¾Ð²ÐµÑ€ÑÐµÐ¼Ñ‹Ð¹ Ð½Ð° Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð½ÑƒÑŽ ÑÑ‚Ñ€ÑƒÐºÑ‚ÑƒÑ€Ñƒ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚
 * @returns {boolean} Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ true/false ÐµÑÐ»Ð¸ item ÑÐ¾Ð´ÐµÑ€Ð¶Ð¸Ñ‚ ÑƒÐºÐ°Ð·Ð°Ð½Ð½Ñ‹Ðµ ÑÐ²Ð¾Ð¹ÑÑ‚Ð²Ð°
 */
export function checkItemStruct(item) {
  if (item && typeof item !== 'object') {
    return false;
  }

  return item.hasOwnProperty('id') && item.hasOwnProperty('title') && item.hasOwnProperty('value');
}

/**
 * Ð’ÑÑ‚Ð°Ð²ÐºÐ° Ð¸Ð·Ð½Ð°Ñ‡Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ñ‚ÐµÐºÑÑ‚Ð° ÑÐµÐ»ÐµÐºÑ‚Ð°(Ð´Ð¾ Ð²Ñ‹Ð±Ð¾Ñ€Ð°)
 * @param {object} data Ð¾Ð±ÑŠÐµÐºÑ‚ Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ Ð½Ð°Ñ…Ð¾Ð´ÑÑ‚ÑŒÑÑ title ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {HTMLElement} select ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÑÐµÐ»ÐµÐºÑ‚Ð°, ÐºÑƒÐ´Ð° Ð±ÑƒÐ´ÐµÑ‚ Ð²ÑÑ‚Ð°Ð²Ð»ÑÑ‚ÑŒÑÑ title
 * @returns {HTMLElement} Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð½Ñ‹Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÑÐµÐ»ÐµÐºÑ‚Ð°
 */
export function getSelectText(data, select) {
  const { placeholder, selected } = data;

  if (placeholder) {
    select.innerText = placeholder;
  } else if (selected) {
    select.innerText = selected;
  } else {
    select.innerText = 'Select...';
  }
  return select;
}

/**
 * ÐŸÑ€ÐµÐ¾Ð±Ñ€Ð°Ð·Ð¾Ð²Ð°Ð½Ð¸Ðµ ÐºÐ°Ð¶Ð´Ð¾Ð³Ð¾ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð½Ð¾Ð³Ð¾ Ð¸Ð· Ð¿Ð¾Ð»Ñ Items;
 * @param {object | string} dataItem Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð½Ñ‹Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ð¹ Ð¿Ñ€Ð¸ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ð¸ ÑÐµÐ»ÐµÐºÑ‚Ð° Ð¼Ð¾Ð¶ÐµÑ‚ Ð±Ñ‹Ñ‚ÑŒ ÐºÐ°Ðº object/string
 * @param {number} index Ð¸Ð½Ð´ÐµÐºÑ ÑÑ‚Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
 * @returns {object} Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð½Ñ‹Ð¹ Ð¾Ð±ÑŠÐµÐºÑ‚
 */
export function getFormatItem(dataItem, index) {
  const random = Math.random().toString(36).substring(2, 10);
  let item = {};

  if (checkItemStruct(dataItem)) {
    item = {
      id: dataItem.id,
      title: dataItem.title,
      value: index,
    };
  } else {
    item = {
      id: random,
      title: dataItem,
      value: index,
    };
  }

  return item;
}

/**
 * ÐŸÐ¾Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾(Ð¾Ð´Ð¸Ð½Ð°Ñ€Ð½Ð¾Ð³Ð¾) ÑÐµÐ»ÐµÐºÑ‚Ð° Ð¿Ñ€Ð¸ Ð²Ñ‹Ð±Ð¾Ñ€Ðµ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð³Ð¾
 * @param {NodeList} element NodeList Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {object} item Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð² ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¼ ÑÐµÐ»ÐµÐºÑ‚Ðµ
 */
export function nativOptionOrdinary(element, item) {
  element.forEach((option) => {
    option.removeAttribute('selected');
    if (option.textContent === item) {
      option.setAttribute('selected', 'selected');
    }
  });
}

/**
 * ÐŸÐ¾Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾(Multiple) ÑÐµÐ»ÐµÐºÑ‚Ð° Ð¿Ñ€Ð¸ Ð²Ñ‹Ð±Ð¾Ñ€Ðµ Ð² ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¼
 * @param {NodeList} element NodeList Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {object} item Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð² ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð¼ ÑÐµÐ»ÐµÐºÑ‚Ðµ
 * @param {boolean} condition ÑÐ¿ÐµÑ†Ð¸Ð°Ð»ÑŒÐ½Ñ‹Ð¹ Ñ„Ð»Ð°Ð³ Ð¿Ñ€Ð¸ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ Ð´Ð¾Ð±Ð°Ð²Ð»ÑÑŽÑ‚ÑÑ/ÑƒÐ±ÐµÑ€Ð°ÑŽÑ‚ÑÑ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ñ‹ Ñƒ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°
 */
export function nativOptionMultiple(element, item, condition) {
  element.forEach((option) => {
    if (condition == true) {
      if (option.textContent === item) {
        option.setAttribute('selected', 'selected');
      }
    } else if (condition == false) {
      if (option.textContent === item) {
        option.removeAttribute('selected');
      }
    } else {
      return;
    }
  });
}

/**
 * Ð¡Ð¾Ð·Ð´Ð°Ð½Ð¸Ðµ ÐºÐ½Ð¾Ð¿ÐºÐ¸ Ð¾Ñ‚Ñ‡Ð¸ÑÐºÐ¸ ÑÐµÐ»ÐµÐºÑ‚Ð°, Ð¿Ñ€Ð¸ ÐµÐ´Ð¸Ð½Ð¸Ñ‡Ð½Ð¾Ð¼ Ð²Ñ‹Ð±Ð¾Ñ€Ðµ.
 * @param {HTMLElement} select Ð¼ÐµÑÑ‚Ð¾ Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ðµ Ð±ÑƒÐ´ÐµÑ‚ Ð¿ÐµÑ€ÐµÐ½Ð°Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¾ Ð½Ð° ''.
 * @param {HTMLElement} element ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€ ÐºÐ»Ð°ÑÑÐ° DropDown.
 * @param {object} dataSelectText Ñ‚ÐµÐºÑÑ‚ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ÑÑ Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ.
 */
export function clearSelect(select, element, dataSelectText) {
  const { selectedItems, indexes, darkTheme, multiselectTag } = dataSelectText;

  const options = element.querySelectorAll('.list__item');
  const ulMultiSelect = element.querySelector('.multiselect-tag');
  const svgIcon = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  const path1 = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  const path2 = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  const checkBox = element.querySelectorAll('li input');

  svgIcon.setAttribute('viewBox', '0 0 10 10');
  path1.setAttribute('d', 'M2,8 L8,2');
  path2.setAttribute('d', 'M2,2 L8,8');
  svgIcon.appendChild(path1);
  svgIcon.appendChild(path2);

  if (multiselectTag && multiselectTag == true) {
    return;
  }

  if (darkTheme === true || !darkTheme) {
    path1.classList.add('pathWhite');
    path2.classList.add('pathWhite');
  }

  if (darkTheme === false) {
    path1.classList.add('pathBlack');
    path2.classList.add('pathBlack');
  }

  svgIcon.classList.add('svg-icon');
  svgIcon.classList.add('svg-clear');

  select.appendChild(svgIcon);

  svgIcon.addEventListener('click', () => {
    select.innerText = '';

    if (Array.isArray(selectedItems)) {
      selectedItems.splice(0);
      indexes.splice(0);
    }

    checkBox.forEach((item) => {
      item.checked = false;
    });

    getSelectText(dataSelectText, select);

    options.forEach((option) => {
      option.classList.remove('active');
    });
  });
}
