/**
 * @class ÐžÐ¿Ð¸ÑÐ°Ð½Ð¸Ðµ ÐºÐ»Ð°ÑÑÐ° DropDown
 * @description Ð­Ñ‚Ð¾Ñ‚ ÐºÐ»Ð°ÑÑ Ñ€ÐµÐ°Ð»Ð¸Ð·Ð¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¾Ð½Ð°Ð» ÐºÐ°ÑÑ‚Ð¾Ð¼Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°, Ñ Ð²Ð¾Ð·Ð¼Ð¾Ð¶Ð½Ð¾ÑÑ‚ÑÐ¼Ð¸ ÐºÐ°ÑÑ‚Ð¾Ð¼Ð¸Ð·Ð°Ñ†Ð¸Ð¸.
 *@author ÐžÐ²ÑÑÐ½Ð¸ÐºÐ¾Ð² ÐœÐ°ÐºÑÐ¸Ð¼
 */
export class DropDown {
  /**
   * Ð¡Ð¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ð¹ HTML ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚
   * @type {HTMLElement}
   */
  #element;
  /**
   * Ð¡Ð¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ð¹ ÑÐ¿Ð¸ÑÐ¾Ðº(ul), Ñ ÐºÐ»Ð°ÑÑÐ¾Ð¼ list
   * @type {HTMLElement}
   */
  #list;
  /**
   * ÐÐ°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ ÑÐµÐ»ÐµÐºÑ‚Ð° Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ðµ Ð¿Ñ€Ð¸ ÑÐ¾Ð·Ð´Ð°Ð½Ð¸Ð¸ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ°
   * @type {object}
   */
  #options;
  /**
   * ÐŸÐµÑ€ÐµÐ¼ÐµÐ½Ð½Ð°Ñ Ð´Ð»Ñ ÑƒÐ¿Ñ€Ð°Ð²Ð»ÐµÐ½Ð¸Ñ ÐºÐ°Ñ€ÐµÑ‚ÐºÐ¸
   * @type {HTMLElement}
   */
  #caret;
  /**
   * ÐœÐ°ÑÑÐ¸Ð² Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²
   * @type {object[]}
   */
  #items;
  /**
   * ÐŸÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ðµ ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€Ð¸Ð¸
   * @type {string}
   */
  #category;
  /**
   * Ð’Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ð¹ Ð¸Ð»Ð¸ Ð¼Ð°ÑÑÐ¸Ð² Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð¸Ð· ÑÐ¿Ð¸ÑÐºÐ°
   * @type {object[] | object}
   */
  #selectedItems;
  /**
   * ÐœÐ°ÑÑÐ¸Ð² Ð¸Ð½Ð´ÐµÐºÑÐ¾Ð² Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð²
   * @type {number[]}
   */
  #indexes = [];

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @returns {string[] | string | null} Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ðµ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚(Ñ‹) Ð² Ð²Ð¸Ð´Ðµ Ð¼Ð°ÑÑÐ¸Ð²Ð°/ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°/null
   * @description Ð“ÐµÑ‚Ñ‚ÐµÑ€ Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÑŽÑ‰Ð¸Ð¹ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ðµ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚(Ñ‹) ÑÐµÐ»ÐµÐºÑ‚Ð°
   */
  get value() {
    return this.#selectedItems ?? null;
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @returns {number | number[]}Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ Ð¸Ð½Ð´ÐµÐºÑÑ‹ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°(Ð¾Ð²) Ð² Ð²Ð¸Ð´Ðµ Ð¼Ð°ÑÑÐ¸Ð²Ð°/Ð¿ÑƒÑÑ‚Ð¾Ð¹ Ð¼Ð°ÑÑÐ¸Ð²
   * @description Ð“ÐµÑ‚Ñ‚ÐµÑ€ Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÑŽÑ‰Ð¸Ð¹ Ð¸Ð½Ð´ÐµÐºÑÑ‹ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ñ… ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°(Ð¾Ð²) ÑÐµÐ»ÐµÐºÑ‚Ð°
   */
  get indexes() {
    return this.#indexes ?? [];
  }

  /**
   *
   * @param {object} options ÐžÐ±ÑŠÐµÐºÑ‚ Ð¿Ñ€Ð¸Ð½Ð¸Ð¼Ð°ÑŽÑ‰Ð¸Ð¹ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ ÑÐµÐ»ÐµÐºÑ‚Ð°
   * @constructor ÐšÐ¾Ð½ÑÑ‚Ñ€ÑƒÐºÑ‚Ð¾Ñ€ ÐºÐ»Ð°ÑÑÐ° DropDown
   * @description  ÐšÐ¾Ð½ÑÑ‚Ñ€ÑƒÐºÑ‚Ð¾Ñ€ Ð¿Ñ€Ð¸Ð½Ð¸Ð¼Ð°ÐµÑ‚ Ð¾Ð±ÑŠÐµÐºÑ‚ Ð¸ Ñ€ÐµÐ½Ð´ÐµÑ€Ð¸Ñ‚ ÑÐµÐ»ÐµÐºÑ‚.
   * @example
   * options = {
   *  selector: 'Ð£Ð½Ð¸ÐºÐ°Ð»ÑŒÐ½Ñ‹Ð¹ ÑÐµÐ»ÐµÐºÑ‚Ð¾Ñ€',
      selected: 'Ð’Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚',
      placeholder: '...',
      items: [string|number|object],
      darkTheme: true/false,
      searchMode: true/false,
      closeOnSelect:  true/false,
      lenguage: 'ru/en',
      styles: {
        head: {
          background: '...',
        },
        list: {...},
        chips: {...},
        caret: {...},
        placeholder: {...},
        lable: {..},
      },
      event: '...',
      url: 'http/...',
      multiselect: true/false,
      multiselectTag: true/false,
   * }
     
   */
  constructor(options = {}) {
    this.#init(options);
    this.#render();
    this.#initEvent();
    this.#closeSelectClick();
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {string | object} item Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÐ¼Ñ‹Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚
   * @description Ð´Ð¾Ð±Ð°Ð²Ð»ÑÐµÑ‚ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ð¹ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð² ÐºÐ¾Ð½ÐµÑ† ÑÐ¿Ð¸ÑÐºÐ° Ð¸ Ð¿ÐµÑ€ÐµÑ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ¿Ð¸ÑÐ¾Ðº. ÐÐµ Ð¼Ð¾Ð¶ÐµÑ‚ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÑŒÑÑ Ð¿Ñ€Ð¸ Ð¿ÐµÑ€ÐµÐ´Ð°Ñ‡Ð¸ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ñ ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€ÑÐ¼Ð¸
   * @method addItem
   */
  addItem(item) {
    if (this.#category) {
      console.log('can`t add item to category');
      return;
    }

    if (!item) {
      return false;
    }

    const index = this.#items.length;

    this.#items.push(getFormatItem(item, index));
    this.#render();
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {number} index Ð¸Ð½Ð´ÐµÐºÑ ÑƒÐ´Ð°Ð»ÑÐµÐ¼Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
   * @description ÑƒÐ´Ð°Ð»ÑÐµÑ‚ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚ Ð¿Ð¾ Ð¸Ð½Ð´ÐµÐºÑÑƒ Ð¸Ð· ÑÐ¿Ð¸ÑÐºÐ° Ð¸ Ð¿ÐµÑ€ÐµÑ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ ÐµÐ³Ð¾. ÐÐµ Ð¼Ð¾Ð¶ÐµÑ‚ Ð¸ÑÐ¿Ð¾Ð»ÑŒÐ·Ð¾Ð²Ð°Ñ‚ÑŒÑÑ Ð¿Ñ€Ð¸ Ð¿ÐµÑ€ÐµÐ´Ð°Ñ‡Ð¸ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ñ ÐºÐ°Ñ‚ÐµÐ³Ð¾Ñ€ÑÐ¼Ð¸.
   * @method deleteItem
   */
  deleteItem(index) {
    if (this.#category) {
      console.log('can`t add item to category');
      return;
    }

    const item = this.#items[index];

    this.#items.splice(index, 1);
    this.#render();
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @description ÑƒÐ´Ð°Ð»ÑÐµÑ‚ Ð²ÑÐµ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ Ð¸Ð· ÑÐ¿Ð¸ÑÐºÐ° Ð¸ Ð¿ÐµÑ€ÐµÑ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ ÐµÐ³Ð¾.
   * @method deleteItemAll
   */
  deleteItemAll() {
    this.#items.splice(0, this.#items.length);
    this.#render();
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {number} index Ð¸Ð½Ð´ÐµÐºÑ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
   * @description  Ð²Ñ‹Ð±Ð¸Ñ€Ð°ÐµÑ‚ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð±ÑƒÐ´ÐµÑ‚ Ð¸Ð·Ð½Ð°Ñ‡Ð°Ð»ÑŒÐ½Ð¾ Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°Ñ‚ÑŒÑÑ Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ
   * @method selectIndex
   */
  selectIndex(index) {
    if (this.#category) {
      console.log('can`t add item to category');
      return;
    }

    const options = this.#element.querySelectorAll('.list__item');

    if (index > options.length) {
      return;
    }

    const select = options[index].innerText;
    this.#render(select);
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {number} numberItem Ð½Ð¾Ð¼ÐµÑ€ Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÐ¼Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
   * @returns {HTMLElement} Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÑÑ‹Ð»ÐºÑƒ Ð½Ð° Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ñ‹Ð¹ HTML ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚
   * @method getElement
   */
  getElement(numberItem) {
    if (numberItem > this.#items.length) {
      return;
    }
    return this.#items[numberItem];
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {boolean} value - ÐŸÐµÑ€ÐµÐ´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ð¹ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€ Ð´Ð»Ñ Ð´Ð¾Ð±Ð°Ð²Ð»ÐµÐ½Ð¸Ñ Ð°Ñ‚Ñ€Ð¸Ð±ÑƒÑ‚Ð° disabled;
   * @description ÐœÐµÑ‚Ð¾Ð´ Ð¿Ð¾Ð·Ð²Ð¾Ð»ÑÑŽÑ‰Ð¸Ð¹ Ð¿ÐµÑ€ÐµÐºÐ»ÑŽÑ‡Ð°Ñ‚ÑŒ ÑÐ¾ÑÑ‚Ð¾ÑÐ½Ð¸Ðµ ÑÐµÐ»ÐµÐºÑ‚Ð° disabled,
   * @method disabled
   */
  disabled(value) {
    if (typeof value !== 'boolean') {
      return;
    }

    const select = this.#element.querySelector('.cg-select');
    if (value === true) {
      this.#element.setAttribute('disabled', true);
      select.classList.add('disabled');
    } else {
      this.#element.removeAttribute('disabled');
      select.classList.remove('disabled');
    }
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {HTMLInputElement} button - HTML ÐºÐ½Ð¾Ð¿ÐºÐ°
   * @param {string} method - Ð¼ÐµÑ‚Ð¾Ð´ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ñ open/close
   * @description ÐœÐµÑ‚Ð¾Ð´ Ð¿Ð¾Ð·Ð²Ð¾Ð»ÑÑŽÑ‰Ð¸Ð¹ Ð¾Ñ‚ÐºÑ€Ñ‹Ð²Ð°Ñ‚ÑŒ/Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°Ñ‚ÑŒ ÑÐµÐ»ÐµÐºÑ‚ Ñ Ð¿Ð¾Ð¼Ð¾Ñ‰ÑŒÑŽ ÐºÐ½Ð¾Ð¿Ð¾Ðº
   * @method buttonControl
   */
  buttonControl(button, method) {
    this.btn = button;
    button.addEventListener('click', () => {
      if (method === 'open') {
        this.#open(true);
      } else if (method === 'close') {
        this.#close();
      } else {
        return;
      }
    });
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @param {object} lenguage Ð¾Ð±ÑŠÐµÐºÑ‚ Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ Ð½Ð°Ñ…Ð¾Ð´ÑÑ‚ÑÑ Ð¿Ð¾Ð»Ñ Ð´Ð»Ñ Ð¿Ð¾Ð´ÐºÐ»ÑŽÑ‡ÐµÐ½Ð¸Ñ ÑÐ·Ñ‹ÐºÐ° Ð¸Ð¼ÐµÐµÑ‚ Ð´Ð²Ð° Ð¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ñ… Ð¿Ð¾Ð»Ñ placeholder, textInListSearch
   * @description Ð¼ÐµÑ‚Ð¾Ð´ Ð¿Ð¾Ð·Ð²Ð¾Ð»ÑÑŽÑ‰Ð¸Ð¹ Ð·Ð°Ð¼ÐµÐ½Ð¸Ñ‚ÑŒ Ð¿Ð»ÐµÐ¹ÑÑ…Ð¾Ð»Ð´ÐµÑ€ Ð² Ð¿Ð¾Ð¸ÑÐºÐµ Ð¸ Ñ‚ÐµÐºÑÑ‚ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ð²Ñ‹Ð²Ð¾Ð´Ð¸Ñ‚ÑÑ ÐµÑÐ»Ð¸ Ð½ÐµÑ‚ Ñ€ÐµÐ·ÑƒÐ»ÑŒÑ‚Ð°Ñ‚Ð°
   * @method addLenguage
   */
  addLenguage(lenguage) {
    const { placeholder, textInListSearch } = lenguage;
    const { searchMode } = this.#options;

    if (searchMode && searchMode == true) {
      const search = this.#element.querySelector('.inputSearch');
      const textNoRezult = this.#element.querySelector('.noRezult');
      const textNode = document.createTextNode(textInListSearch);

      search.setAttribute('placeholder', placeholder);
      search.setAttribute('placeholder', placeholder);

      textNoRezult.innerText = '';
      textNoRezult.appendChild(textNode);
    }
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ Ð¸Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ð¸ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @method #init
   * @member
   * @protected
   * @param {object} options Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ðµ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ ÑÐµÐ»ÐµÐºÑ‚Ð°
   * @description ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´. ÐžÐ±Ñ‰Ð°Ñ Ð¸Ð½Ð¸Ñ†Ð¸Ð°Ð»Ð¸Ð·Ð°Ñ†Ð¸Ñ ÑÐµÐ»ÐµÐºÑ‚Ð°. ÐŸÐ¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ Ð½Ð°ÑÑ‚Ð¾ÐµÐº Ð¸ Ð¿Ñ€ÐµÐ¾Ð±Ñ€Ð°Ð·Ð²Ð°Ð½Ð¸Ðµ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² ÑÐµÐ»ÐµÐºÑ‚Ð°.
   * @example
   *  {
        selector: '.cg-dropdown_one',
        placeholder: 'Ð’Ñ‹Ð±ÐµÑ€Ð¸Ñ‚Ðµ Ð°Ð²Ñ‚Ð¾',
        items: [
          'BMW',
          {
            id: '213sade',
            title: 'Opel',
            value: 1,
          },
          'Mersedes',
          'MAN',
          'max',
        ],
        darkTheme: true,
        multiselect: true,
        multiselectTag: true,
      }
   */
  #init(options) {
    this.#options = options;
    const { items, multiselect, url } = this.#options;

    const elem = document.querySelector(options.selector);

    if (!elem) {
      throw new Error(`Element with selector ${options.selector}`);
    }

    this.#element = elem;

    this.#element.addEventListener('click', (e) => {
      e.preventDefault();
      this.#open();
    });

    this.#items = [];

    if (multiselect && multiselect == true) {
      this.#selectedItems = [];
    }

    if (!items && url) {
      this.#renderUrl();
      return;
    }

    items.forEach((dataItem, index) => {
      if (dataItem.category && dataItem.categoryItems) {
        this.#category = dataItem.category;

        this.#items.push(this.#category);
        dataItem.categoryItems.forEach((categoryItem, indexCategory) => {
          this.#items.push(getFormatItem(categoryItem, indexCategory));
        });
      } else {
        this.#items.push(getFormatItem(dataItem, index));
      }
    });
  }

  /**
   * ÐŸÑ€Ð¸Ð²aÑ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   *
   * @method #initSelected
   * @param {string} select Ð½ÐµÐ¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚. Ð˜ÑÐ¿Ð¾Ð»ÑŒÐ·ÑƒÐµÑ‚ÑÑ Ð² Ð¼ÐµÑ‚Ð¾Ð´Ðµ selectIndex
   * @description ÐžÑ‚Ñ€Ð¸ÑÐ¾Ð²Ñ‹Ð²Ð°ÐµÑ‚ Ð¸ ÑÑ‚Ð¸Ð»Ð¸Ð·ÑƒÐµÑ‚ ÑÐµÐ»ÐµÐºÑ‚
   * @protected
   */
  #initSelected(select) {
    const { styles, selected, placeholder, lable } = this.#options;

    if (selected) {
      createSelected(this.#element, selected);
    } else if (placeholder) {
      createSelected(this.#element, placeholder);
    } else {
      createSelected(this.#element, 'Select...');
    }

    if (select) {
      createSelected(this.#element, select, styles);
    }

    if (lable) {
      const lableItem = document.createElement('h1');
      const textLable = document.createTextNode(lable);

      lableItem.appendChild(textLable);
      lableItem.classList.add('label');

      this.#element.insertAdjacentElement('beforebegin', lableItem);
    }

    if (styles) {
      customStyles(this.#element, styles);
    }
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ Ñ€ÐµÐ½Ð´ÐµÑ€Ð° ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   *@protected
   * @method #render
   * @param {string} select  Ð½ÐµÐ¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ð¹ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚. ÐŸÐµÑ€ÐµÐ´Ð°ÐµÑ‚ÑŒÑÑ Ð² Ð¼ÐµÑ‚Ð¾Ð´ initSelected
   * @description Ð ÐµÐ½Ð´ÐµÑ€ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ.
   */
  #render(select) {
    const { styles, multiselect, searchMode, multiselectTag, darkTheme, lenguage } = this.#options;
    const random = Math.random().toString(36).substring(2, 10);

    if (select || (select && styles)) {
      this.#initSelected(select);
      customStyles(this.#element, styles);
    } else {
      this.#initSelected();
    }

    const ulList = document.createElement('ul');
    const nativSelect = createNativeSelect();

    let intputSearch = '';
    this.random = random;

    if (searchMode) {
      if (lenguage === 'ru') {
        intputSearch = createInputSearch(random, ru.placeholder);
      } else {
        intputSearch = createInputSearch(random, en.placeholder);
      }

      ulList.appendChild(intputSearch);
    }

    ulList.classList.add('list');

    if (styles) {
      const { list } = styles;
      customStylesFormat(list, ulList);
    }

    this.#element.appendChild(ulList);

    this.#items.forEach((dataItem) => {
      this.#element.appendChild(nativSelect);

      const liItem = document.createElement('li');
      const nativOption = createNativSelectOption();
      const strongItem = document.createElement('strong');

      liItem.classList.add('list__item');
      strongItem.classList.add('category');

      if (multiselect && multiselect == true) {
        const checkBox = document.createElement('input');
        checkBox.type = 'checkbox';
        checkBox.setAttribute('id', `chbox-${dataItem.id}`);
        liItem.appendChild(checkBox);

        if (multiselectTag && multiselectTag == true) {
          checkBox.classList.add('displayHide');
        }

        nativSelect.setAttribute('multiple', 'multiple');
      }

      let textNode = '';

      if (dataItem.title) {
        nativOption.text = dataItem.title;
        nativOption.value = dataItem.title;
        textNode = document.createTextNode(dataItem.title);

        nativSelect.appendChild(nativOption);
        liItem.appendChild(textNode);
        ulList.appendChild(liItem);
      } else {
        textNode = document.createTextNode(dataItem);
        strongItem.appendChild(textNode);
        ulList.appendChild(strongItem);
      }
    });

    this.#items.filter((item, index) => {
      if (typeof item !== 'object') {
        this.#items.splice(index, 1);
      }
      return item;
    });

    if (darkTheme == false) {
      this.#checkTheme();
    }

    this.#list = this.#element.querySelector('.list');
    this.#caret = this.#element.querySelector('.caret');

    this.#addOptionsBehaviour();
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ Ñ€ÐµÐ½Ð´ÐµÑ€Ð° ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @method #checkTheme
   * @description Ð˜Ð·Ð¼ÐµÐ½ÑÐµÑ‚ Ñ†Ð²ÐµÑ‚Ð¾Ð²ÑƒÑŽ ÑÑ…ÐµÐ¼Ñƒ Ñ Ñ‚ÐµÐ¼Ð½Ð¾Ð¹ Ð½Ð° ÑÐ²ÐµÑ‚Ð»ÑƒÑŽ.
   */
  #checkTheme() {
    const { darkTheme, searchMode } = this.#options;

    const select = this.#element.querySelector('.cg-select');
    const caret = this.#element.querySelector('.caret');
    const list = this.#element.querySelector('ul.list');
    const search = this.#element.querySelector('.inputSearch');

    if (darkTheme == false) {
      select.classList.add('selectWhite');
      caret.classList.add('caretWhite');
      list.classList.add('listWhite');

      if (searchMode == true) {
        search.classList.add('inputWhite');
      }
    } else if (darkTheme == true || !darkTheme) {
      return;
    } else {
      throw new Error('Styles error or invalid value entered!');
    }
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ Ñ€ÐµÐ½Ð´ÐµÑ€Ð° ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   *@protected
   * @method #renderUrl
   * @description Ð ÐµÐ½Ð´ÐµÑ€ ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ñ… Ñ URL Ð¸ Ð¸Ñ… Ð½Ð°ÑÑ‚Ð¾Ð¹ÐºÐ°
   */
  async #renderUrl() {
    const { url, items, multiselect, multiselectTag } = this.#options;

    if (items) {
      return;
    }

    if (!url) {
      return;
    }

    const response = await fetch(url);
    const dataUrl = await response.json();

    const nativSelect = createNativeSelect();

    dataUrl.forEach((dataItem, index) => {
      const item = {
        id: dataItem.id,
        title: dataItem.name,
        value: index,
      };
      const ulUrl = this.#element.querySelector('.list');

      const nativOption = createNativSelectOption();
      const liUrl = document.createElement('li');
      const textUrl = document.createTextNode(item.title);

      if (multiselect && multiselect == true) {
        const checkBox = document.createElement('input');
        checkBox.type = 'checkbox';
        if (multiselectTag && multiselectTag == true) {
          checkBox.classList.add('displayHide');
        }

        checkBox.setAttribute('id', `chbox-${item.id}`);
        nativSelect.setAttribute('multiple', 'multiple');

        liUrl.appendChild(checkBox);
      }

      liUrl.classList.add('list__item');
      nativOption.value = item.title;
      nativOption.text = item.title;

      nativSelect.appendChild(nativOption);
      liUrl.appendChild(textUrl);
      ulUrl.appendChild(liUrl);

      this.#items.push(item);
    });

    this.#element.appendChild(nativSelect);

    this.#items.filter((item, index) => {
      if (typeof item !== 'object') {
        this.#items.splice(index, 1);
      }
      return item;
    });

    this.#addOptionsBehaviour();
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @param {boolean} oneClick Ð½ÐµÐ¾Ð±ÑÐ·Ð°Ñ‚ÐµÐ»ÑŒÐ½Ñ‹Ð¹ Ð¿Ð°Ñ€Ð°Ð¼ÐµÑ‚Ñ€ Ð¿ÐµÑ€ÐµÐ´Ð°Ð²Ð°ÐµÐ¼Ñ‹Ð¹ Ð¸Ð· Ñ„ÑƒÐ½ÐºÑ†Ð¸Ð¸ buttonControl
   * @description ÐžÑ‚ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ¿Ð¸ÑÐ¾Ðº Ð´Ð»Ñ Ð²Ñ‹Ð±Ð¾Ñ€Ð° ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
   * @method #open
   */
  #open(oneClick) {
    if (oneClick === true) {
      this.#list.classList.add('open');
      this.#caret.classList.add('caret_rotate');
    } else {
      this.#list.classList.toggle('open');
      this.#caret.classList.toggle('caret_rotate');
    }
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @description Ð—Ð°ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ¿Ð¸ÑÐ¾Ðº
   * @method #close
   */
  #close() {
    this.#list.classList.remove('open');
    this.#caret.classList.remove('caret_rotate');
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @description ÐœÐµÑ‚Ð¾Ð´ Ñ€ÐµÐ°Ð»Ð¸Ð·Ð¾Ð²Ñ‹Ð²Ð°ÑŽÑ‰Ð¸Ð¹ Ð²Ñ‹Ð±Ð¾Ñ€ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð² Ñ€Ð°Ð·Ð½Ñ‹Ñ… Ñ€ÐµÐ¶Ð¸Ð¼Ð°Ñ…. ÐžÐ±Ñ‹Ñ‡Ð½Ñ‹Ð¹/ÐœÑƒÐ»ÑŒÑ‚Ð¸ÑÐµÐ»ÐµÐºÑ‚/ÐœÑƒÐ»ÑŒÑ‚Ð¸ÑÐµÐ»ÐµÐºÑ‚ + ÐœÑƒÐ»ÑŒÑ‚Ð¸ÑÐµÐ»ÐµÐºÑ‚ Ð¢Ð°Ð³.
   * @method #addOptionsBehaviour
   */
  #addOptionsBehaviour() {
    const {
      multiselect,
      placeholder,
      selected,
      multiselectTag,
      searchMode,
      closeOnSelect,
      darkTheme,
    } = this.#options;

    const options = this.#element.querySelectorAll('.list__item');
    const select = this.#element.querySelector('.selected');
    const nativOption = this.#element.querySelectorAll('.nativSelect__nativOption');

    const ulMultipul = document.createElement('ul');

    if (multiselect && multiselect == true) {
      ulMultipul.classList.add('multiselect-tag');
      select.classList.add('overflow-hidden');
    }

    if (searchMode && searchMode === true) {
      this.#searchMode(this.random);
    }

    options.forEach((option, index) => {
      option.addEventListener('click', (event) => {
        const dataSelectText = {
          placeholder,
          selected,
          selectedItems: this.#selectedItems,
          indexes: this.#indexes,
          darkTheme,
          multiselectTag,
        };

        const item = this.#items[index];

        if (closeOnSelect == false || (multiselect && multiselect == true)) {
          event.stopPropagation();
          event.preventDefault();
        }

        const checkIndex = this.#indexes.indexOf(index);

        if (multiselect && multiselect == true) {
          option.classList.toggle('active');
          const checkBox = option.querySelector('input[type="checkbox"]');

          if (checkBox) {
            if (!(event.target instanceof HTMLInputElement)) {
              checkBox.checked = !checkBox.checked;
            }

            if (checkIndex === -1) {
              nativOptionMultiple(nativOption, item.title, true);
              this.#indexes.push(index);
              select.innerText = '';

              if (multiselectTag && multiselectTag == true) {
                this.#selectedItems.push(item);
                select.appendChild(ulMultipul);

                const data = {
                  option: this.#options,
                  element: this.#element,
                  indexes: this.#indexes,
                  selectedItems: this.#selectedItems,
                };

                ulMultipul.appendChild(createBreadcrumb(data, item.title, index, item.id));
              } else {
                this.#selectedItems.push(item.title);
                select.innerText = this.#selectedItems;
              }
            } else {
              if (multiselectTag && multiselectTag == true) {
                const tagItem = document.getElementById(`tag-${index}-${item.id}`);
                ulMultipul.removeChild(tagItem);
              }

              this.#indexes.splice(checkIndex, 1);
              this.#selectedItems.splice(checkIndex, 1);
              nativOptionMultiple(nativOption, item.title, false);
            }

            if (!this.#selectedItems.length) {
              getSelectText(dataSelectText, select);
            } else {
              if (multiselectTag && multiselectTag == true) {
                select.appendChild(ulMultipul);
              } else {
                select.innerText = this.#selectedItems;
              }
            }
          }
        } else {
          select.innerText = item.title;
          this.#selectedItems = item;

          nativOptionOrdinary(nativOption, item.title);

          options.forEach((option) => {
            option.classList.remove('active');
          });
          option.classList.add('active');
        }

        clearSelect(select, this.#element, dataSelectText);
      });
    });
  }

  /**
   * ÐœÐµÑ‚Ð¾Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ Ñ€ÐµÐ°Ð»Ð¸Ð·ÑƒÐµÑ‚ Ð¿Ð¾Ð¸ÑÐº ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ
   * @protected
   * @param {string} random ÑƒÐ½Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ðµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð´Ð»Ñ input ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°.
   * @method #searchMode
   */
  #searchMode(random) {
    const { lenguage } = this.#options;

    const input = this.#element.querySelector(`#searchSelect-${random}`);
    const searchSelect = this.#element.querySelectorAll('.list__item');
    const result = document.createElement('p');

    let textNode = '';
    if (lenguage === 'ru') {
      textNode = document.createTextNode(`${ru.textInListSearch}`);
    } else {
      textNode = document.createTextNode(`${en.textInListSearch}`);
    }

    result.appendChild(textNode);
    result.classList.add('displayHide');
    result.classList.add('noRezult');
    input.parentElement.appendChild(result);

    input.addEventListener('click', (e) => {
      e.stopPropagation();
    });

    input.oninput = function () {
      let val = this.value.trim();

      if (val != '') {
        searchSelect.forEach((elem) => {
          if (elem.innerText.search(val) == -1) {
            elem.classList.add('displayHide');
            result.classList.remove('displayHide');
          } else {
            elem.classList.remove('displayHide');
          }
        });
      } else {
        searchSelect.forEach((elem) => {
          elem.classList.remove('displayHide');
          result.classList.add('displayHide');
        });
      }
    };
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @description ÐžÑ‚ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ Ð¸ Ð·Ð°ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ¿Ð¸ÑÐ¾Ðº Ð¿Ð¾ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ð¾Ð¼Ñƒ ÑÐ²ÐµÐ½Ñ‚Ñƒ
   * @method #initEvent
   */
  #initEvent() {
    const { event } = this.#options;
    if (!event) {
      return;
    }

    if (event) {
      if (event === 'mouseenter') {
        this.#element.addEventListener(event, () => {
          this.#open();
        });
        this.#element.addEventListener('mouseleave', () => {
          this.#close();
        });
      }
    }
  }

  /**
   * ÐŸÑ€Ð¸Ð²Ð°Ñ‚Ð½Ñ‹Ð¹ Ð¼ÐµÑ‚Ð¾Ð´ ÑÐºÐ·ÐµÐ¼Ð¿Ð»ÑÑ€Ð° ÐºÐ»Ð°ÑÑÐ° DropDown
   * @protected
   * @description Ð—Ð°ÐºÑ€Ñ‹Ð²Ð°ÐµÑ‚ ÑÐ¿Ð¸ÑÐ¾Ðº Ð¿Ð¾ ÐºÐ»Ð¸ÐºÑƒ Ð²Ð½Ðµ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
   * @method #closeSelectClick
   */
  #closeSelectClick() {
    const dropdown = document.querySelector(`${this.#options.selector}`);

    document.addEventListener('click', (e) => {
      const withinBoundaries = e.composedPath().includes(dropdown);
      if (!withinBoundaries) {
        if (this.btn) {
          return;
        } else {
          this.#close();
        }
      }
    });
  }
}
