import { customStylesFormat, nativOptionMultiple } from './utils';
/**
 * @module createBreadcrumb
 */

/**
 * ÐœÐµÑ‚Ð¾Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ ÑÐ¾Ð·Ð´Ð°ÐµÑ‚ Ð¸ Ð¾Ñ‚Ð²ÐµÑ‡Ð°ÐµÑ‚ Ð·Ð° Ð¿Ð¾Ð²ÐµÐ´ÐµÐ½Ð¸Ðµ chips
 * @param {object} data Ð¾Ð±ÑŠÐµÐºÑ‚ Ð² ÐºÐ¾Ñ‚Ð¾Ñ€Ð¾Ð¼ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑÑ Ð½Ð°ÑÑ‚Ñ€Ð¾Ð¹ÐºÐ¸ Ð¸ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ñ‹ ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @param {string} title Ð¸Ð¼Ñ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð´Ð»Ñ Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²ÐºÐ¸ chips
 * @param {number} index Ð¸Ð½Ð´ÐµÐºÑ Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð° Ð´Ð»Ñ Ð¾Ñ‚Ñ€Ð¸ÑÐ¾Ð²ÐºÐ¸ chips
 * @param {string} id ÑƒÐ½Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ðµ id Ð²Ñ‹Ð±Ñ€Ð°Ð½Ð½Ð¾Ð³Ð¾ ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°
 * @returns {HTMLElement} Ð²Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð½Ñ‹Ð¹ HTMLElement chips item
 */
export function createBreadcrumb(data, title, index, id) {
  const { element, option, indexes, selectedItems } = data;
  const { placeholder, styles } = option;

  const selected = element.querySelector('.selected');
  const nativOption = element.querySelectorAll('.nativSelect__nativOption');

  const liChip = document.createElement('li');
  const textNode = document.createTextNode(title);
  const svgIcon = document.createElementNS('http://www.w3.org/2000/svg', 'svg');
  const path1 = document.createElementNS('http://www.w3.org/2000/svg', 'path');
  const path2 = document.createElementNS('http://www.w3.org/2000/svg', 'path');

  svgIcon.setAttribute('viewBox', '0 0 10 10');
  path1.setAttribute('d', 'M3,7 L7,3');
  path2.setAttribute('d', 'M3,3 L7,7');
  liChip.setAttribute('id', `tag-${index}-${id}`);

  svgIcon.classList.add('svg-icon');

  svgIcon.appendChild(path1);
  svgIcon.appendChild(path2);
  liChip.appendChild(textNode);
  liChip.appendChild(svgIcon);

  if (styles) {
    const { chips } = styles;
    customStylesFormat(chips, liChip);
  }

  svgIcon.addEventListener('click', (event) => {
    event.preventDefault();
    event.stopPropagation();
    nativOptionMultiple(nativOption, title, false);

    const deleteIcon = indexes.indexOf(index);
    let checkBox = '';

    indexes.splice(deleteIcon, 1);
    selectedItems.splice(deleteIcon, 1);

    if (id) {
      checkBox = document.getElementById(`chbox-${id}`);
    } else {
      checkBox = document.getElementById(`chbox-${index}`);
    }

    checkBox.checked = false;
    checkBox.parentElement.classList.remove('active');

    if (!selectedItems.length) {
      selected.innerText = placeholder;
    }

    liChip.parentElement.removeChild(liChip);
  });

  return liChip;
}

/**
 * ÐœÐµÑ‚Ð¾Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ ÑÐ¾Ð·Ð´Ð°ÐµÑ‚ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ñ‹Ð¹ ÑÐµÐ»ÐµÐºÑ‚
 * @returns {HTMLSelectElement} Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÐ¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ð¹ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ñ‹Ð¹ ÑÐµÐ»ÐµÐºÑ‚
 */
export function createNativeSelect() {
  const nativSelect = document.createElement('select');

  nativSelect.setAttribute('name', 'dataSelect');
  nativSelect.classList.add('nativSelect');
  return nativSelect;
}

/**
 * ÐœÐµÑ‚Ð¾Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ ÑÐ¾Ð·Ð´Ð°ÐµÑ‚ Options Ð´Ð»Ñ Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°
 * @returns {HTMLOptionElement} Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÐ¾Ð·Ð´Ð°Ð½Ð½Ñ‹Ðµ Options Ð½Ð°Ñ‚Ð¸Ð²Ð½Ð¾Ð³Ð¾ ÑÐµÐ»ÐµÐºÑ‚Ð°
 */
export function createNativSelectOption() {
  const nativOption = document.createElement('option');

  nativOption.classList.add('nativSelect__nativOption');
  return nativOption;
}

/**
 * ÐœÐµÑ‚Ð¾Ð´ ÐºÐ¾Ñ‚Ð¾Ñ€Ñ‹Ð¹ ÑÐ¾Ð·Ð´Ð°ÐµÑ‚ Ð¿Ð¾Ð¸ÑÐº ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð¾Ð² Ð² ÑÐµÐ»ÐµÐºÑ‚Ðµ
 * @param {string} random ÑƒÐ½Ð¸ÐºÐ°Ð»ÑŒÐ½Ð¾Ðµ Ð·Ð½Ð°Ñ‡ÐµÐ½Ð¸Ðµ Ð´Ð»Ñ input ÑÐ»ÐµÐ¼ÐµÐ½Ñ‚Ð°.
 * @param {string} lenguage Ñ‚ÐµÐºÑÑ‚ Ð½Ð° Ð¾Ð¿Ñ€ÐµÐ´ÐµÐ»ÐµÐ½Ð½Ð¾Ð¼ ÑÐ·Ñ‹ÐºÐµ Ð¿ÐµÑ€ÐµÐ´Ð°Ð½Ð½Ñ‹Ð¹ Ð¸Ð· Ñ„Ð°Ð¹Ð»Ð° language.js
 * @returns {HTMLInputElement} Ð’Ð¾Ð·Ð²Ñ€Ð°Ñ‰Ð°ÐµÑ‚ ÑÑ„Ð¾Ñ€Ð¼Ð¸Ñ€Ð¾Ð²Ð°Ð½Ð½Ñ‹Ð¹ input ÐµÐ»ÐµÐ¼ÐµÐ½Ñ‚.
 */
export function createInputSearch(random, lenguage) {
  const intputSearch = document.createElement('input');

  intputSearch.type = 'text';
  intputSearch.classList.add('inputSearch');
  intputSearch.setAttribute('id', `searchSelect-${random}`);

  if (lenguage) {
    intputSearch.setAttribute('placeholder', `${lenguage}`);
  } else {
    intputSearch.setAttribute('placeholder', 'Search...');
  }

  intputSearch.addEventListener('click', (e) => {
    e.preventDefault();
  });

  return intputSearch;
}
